export enum SpellsTypeEnum {
  /**  */
  Active,
  /**  */
  Passive,
}

export interface Spells {
  /**  */
  name: string;
  key: string;
  /**  */
  cost?: number;
  /**  */
  coolDown?: number;
  /**  */
  type: SpellsTypeEnum;
  /**  */
  description: string;
  icon: string;
  iconType?: "light" | "dark";
  video: string;
}

export interface SpellsTreeNode {
  label: string;
  children: Spells[];
}
